.background {
  border-radius: 24px;
  width: 900px;
  height: 704px;
  background-color: #fff;
  padding: 25px 24px 0px 24px;
  box-sizing: border-box;
}
.header {
  display: flex;
  gap: 24px;
  padding-bottom: 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
}
.header-img {
  width: 80px;
  height: 80px;
}
.header-title {
  color: #333;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 26px;
}
.header-items {
  display: flex;
  gap: 16px;
  margin-top: 8px;
}
.header-item {
  display: flex;
  gap: 4px;
}
.header-item-text {
  color: #5558fa;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
}
.main-input {
  padding-top: 24px;
}
.main-input input {
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.16);
  background-color: rgba(0, 0, 0, 0.04);
  width: 400px;
  height: 44px;
  padding: 12px;
  box-sizing: border-box;
}
.main-input label {
  display: block;
  margin-bottom: 8px;
  color: #333;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
}
.button-start {
  margin-top: 48px;
  outline: none;
  border: none;
  display: inline-flex;
  padding: 12px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 4px;
  background: #5558fa;
  color: #fff;
}
.main-input p {
  color: red;
  margin-top: 10px;
}
